const { ContractRepository } = require('../repositories/ContractRepository');
const { ClientRepository } = require('../repositories/ClientRepository');
const { calculatePaymentPercentage } = require('../utils/contractCalculations');

class ContractService {
  /**
   * Obter lista de contratos com paginação e filtros
   * @param {Object} options - Opções de busca
   * @returns {Object} Lista paginada de contratos
   */
  static async getContracts(options) {
    try {
      // Validar parâmetros
      const { page, limit, search, status, clientId, sortBy, sortOrder } = options;
      
      if (page < 1 || limit < 1 || limit > 100) {
        throw new Error('Parâmetros de paginação inválidos');
      }

      const validSortFields = ['created_at', 'updated_at', 'contract_number', 'value', 'status'];
      if (!validSortFields.includes(sortBy)) {
        throw new Error('Campo de ordenação inválido');
      }

      const validSortOrders = ['asc', 'desc'];
      if (!validSortOrders.includes(sortOrder)) {
        throw new Error('Ordem de classificação inválida');
      }

      return await ContractRepository.getContracts(options);
    } catch (error) {
      console.error('Erro no ContractService.getContracts:', error);
      throw error;
    }
  }

  /**
   * Obter contrato por ID
   * @param {string} id - ID do contrato
   * @returns {Object|null} Dados do contrato
   */
  static async getContractById(id) {
    try {
      if (!id) {
        throw new Error('ID do contrato é obrigatório');
      }

      const contract = await ContractRepository.getContractById(id);
      
      if (contract) {
        // Calcular estatísticas do contrato
        const stats = await this.calculateContractStats(contract);
        contract.stats = stats;
      }

      return contract;
    } catch (error) {
      console.error('Erro no ContractService.getContractById:', error);
      throw error;
    }
  }

  /**
   * Criar novo contrato
   * @param {Object} contractData - Dados do contrato
   * @returns {Object} Contrato criado
   */
  static async createContract(contractData) {
    try {
      // Validar dados obrigatórios
      const requiredFields = ['client_id', 'value', 'installments', 'first_due_date'];
      for (const field of requiredFields) {
        if (!contractData[field]) {
          throw new Error(`Campo ${field} é obrigatório`);
        }
      }

      // Validar se o cliente existe
      const client = await ClientRepository.getClientById(contractData.client_id);
      if (!client) {
        throw new Error('Cliente não encontrado');
      }

      // Validar valores numéricos
      if (parseFloat(contractData.value) <= 0) {
        throw new Error('Valor do contrato deve ser maior que zero');
      }

      if (parseInt(contractData.installments) <= 0) {
        throw new Error('Número de parcelas deve ser maior que zero');
      }

      // Validar data de vencimento
      const firstDueDate = new Date(contractData.first_due_date);
      if (isNaN(firstDueDate.getTime())) {
        throw new Error('Data de primeiro vencimento inválida');
      }

      // Verificar se já existe contrato com mesmo número
      if (contractData.contract_number) {
        const existingContract = await ContractRepository.getContractByNumber(contractData.contract_number);
        if (existingContract) {
          throw new Error('Já existe um contrato com este número');
        }
      }

      // Preparar dados para criação
      const newContractData = {
        ...contractData,
        value: parseFloat(contractData.value),
        installments: parseInt(contractData.installments),
        down_payment: parseFloat(contractData.down_payment || 0),
        status: contractData.status || 'active',
        created_at: new Date().toISOString(),
        updated_at: new Date().toISOString()
      };

      // Gerar número do contrato se não fornecido
      if (!newContractData.contract_number) {
        newContractData.contract_number = await this.generateContractNumber();
      }

      return await ContractRepository.createContract(newContractData);
    } catch (error) {
      console.error('Erro no ContractService.createContract:', error);
      throw error;
    }
  }

  /**
   * Atualizar contrato
   * @param {string} id - ID do contrato
   * @param {Object} updateData - Dados para atualização
   * @returns {Object|null} Contrato atualizado
   */
  static async updateContract(id, updateData) {
    try {
      if (!id) {
        throw new Error('ID do contrato é obrigatório');
      }

      // Verificar se o contrato existe
      const existingContract = await ContractRepository.getContractById(id);
      if (!existingContract) {
        return null;
      }

      // Validar cliente se estiver sendo alterado
      if (updateData.client_id && updateData.client_id !== existingContract.client_id) {
        const client = await ClientRepository.getClientById(updateData.client_id);
        if (!client) {
          throw new Error('Cliente não encontrado');
        }
      }

      // Validar número do contrato se estiver sendo alterado
      if (updateData.contract_number && updateData.contract_number !== existingContract.contract_number) {
        const duplicateContract = await ContractRepository.getContractByNumber(updateData.contract_number);
        if (duplicateContract && duplicateContract.id !== id) {
          throw new Error('Já existe um contrato com este número');
        }
      }

      // Validar valores numéricos
      if (updateData.value !== undefined && parseFloat(updateData.value) <= 0) {
        throw new Error('Valor do contrato deve ser maior que zero');
      }

      if (updateData.installments !== undefined && parseInt(updateData.installments) <= 0) {
        throw new Error('Número de parcelas deve ser maior que zero');
      }

      // Preparar dados para atualização
      const contractUpdate = {
        ...updateData,
        updated_at: new Date().toISOString()
      };

      if (contractUpdate.value !== undefined) {
        contractUpdate.value = parseFloat(contractUpdate.value);
      }

      if (contractUpdate.installments !== undefined) {
        contractUpdate.installments = parseInt(contractUpdate.installments);
      }

      if (contractUpdate.down_payment !== undefined) {
        contractUpdate.down_payment = parseFloat(contractUpdate.down_payment);
      }

      return await ContractRepository.updateContract(id, contractUpdate);
    } catch (error) {
      console.error('Erro no ContractService.updateContract:', error);
      throw error;
    }
  }

  /**
   * Deletar contrato
   * @param {string} id - ID do contrato
   * @returns {boolean} Sucesso da operação
   */
  static async deleteContract(id) {
    try {
      if (!id) {
        throw new Error('ID do contrato é obrigatório');
      }

      // Verificar se o contrato existe
      const existingContract = await ContractRepository.getContractById(id);
      if (!existingContract) {
        return false;
      }

      // Verificar se o contrato possui pagamentos
      const hasPayments = await ContractRepository.contractHasPayments(id);
      if (hasPayments) {
        throw new Error('Não é possível deletar contrato que possui pagamentos');
      }

      return await ContractRepository.deleteContract(id);
    } catch (error) {
      console.error('Erro no ContractService.deleteContract:', error);
      throw error;
    }
  }

  /**
   * Obter pagamentos do contrato
   * @param {string} contractId - ID do contrato
   * @param {Object} options - Opções de busca
   * @returns {Object} Lista paginada de pagamentos
   */
  static async getContractPayments(contractId, options) {
    try {
      if (!contractId) {
        throw new Error('ID do contrato é obrigatório');
      }

      // Verificar se o contrato existe
      const contract = await ContractRepository.getContractById(contractId);
      if (!contract) {
        throw new Error('Contrato não encontrado');
      }

      return await ContractRepository.getContractPayments(contractId, options);
    } catch (error) {
      console.error('Erro no ContractService.getContractPayments:', error);
      throw error;
    }
  }

  /**
   * Gerar parcelas do contrato
   * @param {string} contractId - ID do contrato
   * @returns {Array} Lista de parcelas geradas
   */
  static async generateInstallments(contractId) {
    try {
      if (!contractId) {
        throw new Error('ID do contrato é obrigatório');
      }

      const contract = await ContractRepository.getContractById(contractId);
      if (!contract) {
        throw new Error('Contrato não encontrado');
      }

      // Verificar se já existem pagamentos para este contrato
      const existingPayments = await ContractRepository.getContractPayments(contractId, { page: 1, limit: 1 });
      if (existingPayments.payments.length > 0) {
        throw new Error('Contrato já possui parcelas geradas');
      }

      return await ContractRepository.generateInstallments(contractId);
    } catch (error) {
      console.error('Erro no ContractService.generateInstallments:', error);
      throw error;
    }
  }

  /**
   * Obter estatísticas do contrato
   * @param {string} contractId - ID do contrato
   * @returns {Object} Estatísticas do contrato
   */
  static async getContractStats(contractId) {
    try {
      if (!contractId) {
        throw new Error('ID do contrato é obrigatório');
      }

      const contract = await ContractRepository.getContractById(contractId);
      if (!contract) {
        throw new Error('Contrato não encontrado');
      }

      return await this.calculateContractStats(contract);
    } catch (error) {
      console.error('Erro no ContractService.getContractStats:', error);
      throw error;
    }
  }

  /**
   * Buscar contratos por termo
   * @param {string} query - Termo de busca
   * @param {number} limit - Limite de resultados
   * @returns {Array} Lista de contratos
   */
  static async searchContracts(query, limit) {
    try {
      if (!query || query.trim().length < 2) {
        throw new Error('Termo de busca deve ter pelo menos 2 caracteres');
      }

      if (limit < 1 || limit > 50) {
        throw new Error('Limite deve estar entre 1 e 50');
      }

      return await ContractRepository.searchContracts(query.trim(), limit);
    } catch (error) {
      console.error('Erro no ContractService.searchContracts:', error);
      throw error;
    }
  }

  /**
   * Atualizar status do contrato
   * @param {string} id - ID do contrato
   * @param {string} status - Novo status
   * @returns {Object|null} Contrato atualizado
   */
  static async updateContractStatus(id, status) {
    try {
      if (!id) {
        throw new Error('ID do contrato é obrigatório');
      }

      const validStatuses = ['active', 'completed', 'cancelled', 'suspended'];
      if (!validStatuses.includes(status)) {
        throw new Error('Status inválido');
      }

      return await this.updateContract(id, { status });
    } catch (error) {
      console.error('Erro no ContractService.updateContractStatus:', error);
      throw error;
    }
  }

  /**
   * Gerar número do contrato
   * @returns {string} Número do contrato
   */
  static async generateContractNumber() {
    try {
      const year = new Date().getFullYear();
      const lastContract = await ContractRepository.getLastContractByYear(year);
      
      let sequence = 1;
      if (lastContract && lastContract.contract_number) {
        const lastSequence = parseInt(lastContract.contract_number.split('/')[0]);
        sequence = lastSequence + 1;
      }

      return `${sequence.toString().padStart(6, '0')}/${year}`;
    } catch (error) {
      console.error('Erro no ContractService.generateContractNumber:', error);
      throw error;
    }
  }

  /**
   * Calcular estatísticas do contrato
   * @param {Object} contract - Dados do contrato
   * @returns {Object} Estatísticas calculadas
   */
  static async calculateContractStats(contract) {
    try {
      // Buscar pagamentos do contrato
      const paymentsResult = await ContractRepository.getContractPayments(contract.id, { page: 1, limit: 1000 });
      const payments = paymentsResult.payments || [];

      // Usar função de cálculo existente
      const calculations = calculatePaymentPercentage(contract, payments);

      return {
        total_value: contract.value,
        down_payment: contract.down_payment || 0,
        installment_value: calculations.installment_value,
        total_payments: payments.length,
        paid_payments: calculations.paid_installments,
        pending_payments: calculations.remaining_installments,
        total_paid: calculations.paid_amount,
        total_pending: calculations.remaining_amount,
        payment_percentage: calculations.percentage_paid,
        is_completed: calculations.is_completed,
        overdue_payments: payments.filter(p => {
          return p.status === 'pending' && p.due_date < new Date().toISOString().split('T')[0];
        }).length
      };
    } catch (error) {
      console.error('Erro no ContractService.calculateContractStats:', error);
      throw error;
    }
  }
}

module.exports = { ContractService };